"use client";

import { useState } from "react";
import BarcodeScanner from "../../components/BarcodeScanner";
import FoodAutocomplete from "../../components/FoodAutocomplete";
import { findFoodByBarcode } from "@/lib/findFoodByBarcode";

export default function LogFoodPage() {
  const [showScanner, setShowScanner] = useState(false);
  const [foodName, setFoodName] = useState("");
  const [calories, setCalories] = useState("");
  const [category, setCategory] = useState("");

  function handleAutocompleteSelect(item) {
    setFoodName(item.name);
    setCalories(item.calories);
    setCategory(item.category);
  }

  function handleBarcodeDetected(code) {
    const match = findFoodByBarcode(code);
    if (match) {
      setFoodName(match.name);
      setCalories(match.calories);
      setCategory(match.category);
    } else {
      alert(`Unknown barcode: ${code}`);
    }
    setShowScanner(false);
  }

  async function handleSubmit(e) {
    e.preventDefault();

    await fetch("/api/food/log/route", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ name: foodName, calories, category }),
    });

    setFoodName("");
    setCalories("");
    setCategory("");
  }

  return (
    <main className="min-h-screen bg-slate-50 px-6 py-10">
      <h1 className="text-3xl font-semibold mb-8">Log Food</h1>

      {showScanner && (
        <BarcodeScanner
          onDetected={handleBarcodeDetected}
          onClose={() => setShowScanner(false)}
        />
      )}

      <form
        onSubmit={handleSubmit}
        className="bg-white border rounded-xl shadow-xl p-6 max-w-xl mx-auto flex flex-col gap-4"
      >
        <FoodAutocomplete onSelect={handleAutocompleteSelect} />

        <input
          value={foodName}
          onChange={e => setFoodName(e.target.value)}
          placeholder="Food name"
          className="border px-4 py-2 rounded-lg"
        />

        <input
          value={calories}
          type="number"
          onChange={e => setCalories(e.target.value)}
          placeholder="Calories"
          className="border px-4 py-2 rounded-lg"
        />

        <input
          value={category}
          onChange={e => setCategory(e.target.value)}
          placeholder="Category"
          className="border px-4 py-2 rounded-lg"
        />

        <button
          type="button"
          onClick={() => setShowScanner(true)}
          className="py-2 px-4 rounded-lg border border-blue-700 text-blue-700 hover:bg-blue-50 transition"
        >
          Scan Barcode
        </button>

        <button
          type="submit"
          className="py-3 bg-blue-700 text-white font-medium rounded-lg hover:bg-blue-800 transition"
        >
          Log Food
        </button>
      </form>
    </main>
  );
}
